<template>
  <div>
 <ve-bar  :data="chartData"
  :extend ="chartExtend"></ve-bar>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
   data () {

        this.chartExtend = {
            series (v) {
                v.forEach((i, index )=> {
                    i.itemStyle = {
                        normal: {
                            color: function(params) {
                            // build a color map as your need.
                            var colorList = [
                              '#FCCE10'
                            ];
                            return colorList[params.dataIndex]
                        },
                        },

                    }
                    console.log(i,index)
                })
                return v
            },
      }
      return {
        chartData: {
          columns: ['項目', '資料庫',],
          rows: [
          //  { '項目': 'mySQl', '資料庫': 95, },
            { '項目': 'Firebase', '資料庫': 70,  },
          ]
        }
      }
    }

}
</script>



