<template>
  <div class="container">
        <div class="card">
            <div class="card-header">
                <h3>Add Item</h3>
            </div>
            <div class="card-body">
              <!--POST-->
                <form @submit.prevent="addProduct">
                    <div class="form-group">
                        <label>Name:</label>
                        <input type="text" class="form-control" v-model="product.name"/>
                    </div>
                    <div class="form-group">
                        <label>description:</label>
                        <input type="text" class="form-control" v-model="product.desc"/>
                    </div>
                    <div class="form-group">
                        <input type="submit" class="btn btn-primary" value="Add Item"/>
                    </div>
                </form>
            </div>

            <ul>
              <li v-for="item in messages" :key="item.id">
                {{item.name}} - {{item.desc}}-{{item.num}}

              </li>
            </ul>
        </div>
  </div>
</template>

<script>
import {db} from "@/config/firebaseConfig";

import firebase from 'firebase/compat/app'
const productsRef = firebase.database().ref("/products")
export default {
  data () {
    return {
      products:[],
      product:{
         name: '',
         desc: '',
      },
      submitted: false,
      currentProduct: null,
      currentIndex: -1,
      messages:''

    }
  },

    methods: {    
      addProduct(e) {
        e.preventDefault(); 
    db.ref('products').push({
      num:`${new Date().toLocaleDateString()} ${new Date().toLocaleTimeString()}`,
      name:this.product.name,
      desc:this.product.desc,
    }).then(() => {console.log('');});
      
      },
      onDataChange() {
 /* let _products = [];
      items.forEach((item) => {
        let key = item.key;
        let data = item.val();
        _products.push({
          key: key,
          title: data.title,
          description: data.description,
          published: data.published,
        });
      });
      this.products = _product;*/
        
       productsRef.once('value',function(snapshot){
         let products = [];
         snapshot.forEach(function(childSnapshot) {
            var key = childSnapshot.key;
            var data = childSnapshot.val();
            console.log(data);
            products.push({ 
              key: key, 
              name: data.name, 
              desc: data.desc
            });
         })
       })

      },

      getProducts () {
      const vm= this;  
      productsRef.on('value', function(snapshot) {
        vm.messages = snapshot.val();
      })
    },
    },
    created(){
        this.addProduct();
      
    }, 
    mounted() {
     this.getProducts();
  },
}
</script>




