<template>
  <div id="app" class="container-flouid">
     <loading />
   <NavbarNew/>
    <router-view></router-view>
  </div>
</template>

<script>

import 'animate.css/animate.css'
import Navbar from '@/components/layouts/Navbar'
import NavbarNew from '@/components/layouts/Navbar-New'
import WOW from 'wow.js'
export default {
  name: 'App',
   data () {
    return {
      isLoading:false,
      values:'',
      colors:['#00CC99', '#CC0066', '#FF6600', '#3366CC','#FFCC00','#663399'],
        chartData: {
        columns: [ '項目','访问用户'],
        rows: [

          { '項目': 'Scss & Css', '访问用户':92},
        ]
      },
    }
  },
  methods:{
    getValue(){
        //${process.edv.APIPATH}
        const api =`${process.env.APIPATH}`;
        const vm =this;
        vm.isLoading=true;
        this.$http.get(api).then((response) => {
          console.log(response.data)
          vm.values=response.data;
          vm.isLoading=false;
        })
    }
  },
  components: {
    Navbar,NavbarNew
  },
  computed: {

  },
  created() {
    new WOW().init();
    this. getValue();
  }


}
</script>


<style lang="scss">
@import"./assets/scss/all";
</style>



