<template>
<div>
     <ve-liquidfill :data="chartData" :settings="chartSettings" ve-liquidfill/>
</div>

</template>

<script>
  export default {
    data () {
        /*this.title = {
          text: "後端技術",
          x: "center",
          textStyle: {
            color: "#000",//標題颜色
            fontSize: 14 , //字體大小
          }
         };*/
      this.chartSettings = {
        seriesMap: [
          {
              //波浪數據
            data: [0.7, 0.6, 0.5],
            color : [ '#4caf50', '#40b883' , '#8bc34a', 'rgb(0, 0, 255)' ] ,
            itemStyle: {
                opacity: 0.6
            },
            emphasis: {
                itemStyle: {
                    opacity: 1
                }
            },
            center: ['50%', '50%'],
            radius: '50%',
            waveAnimation: 10,
            amplitude: 20, // 振幅
            // 設置背景色
            backgroundStyle: {
                borderColor: '#fff',
                borderWidth: 1,
                shadowColor: '#fff',
                shadowBlur: 20,
                // 環狀裡的背景色
                color: '#fff'
            },
            outline: {
                show: true,
                borderDistance: 5,
                itemStyle: {
                    borderColor: '#fff',
                    borderWidth: 2,
                },

            },
          },
          // {
          //   data: [0.72, 0.6, 0.5],
          //   color : [ '#7853b2', '#9c40f1' , 'blueviolet', 'rgb(0, 0, 255)' ] ,
          //   itemStyle: {
          //       opacity: 0.8
          //   },
          //   emphasis: {
          //       itemStyle: {
          //           opacity: 1
          //       }
          //   },
          //   center: ['75%', '50%'],
          //   radius: '50%',
          //   waveAnimation: 10,
          //   amplitude: 50, // 振幅
          //   // 設置背景色
          //   backgroundStyle: {
          //       borderColor: '#fff',
          //       borderWidth: 1,
          //       shadowColor: '#fff',
          //       shadowBlur: 20,
          //       //環狀裡的背景色
          //       color: '#fff'
          //   },
          //   outline: {
          //       show: true,
          //       borderDistance: 5,
          //       itemStyle: {
          //           borderColor: '#fff',
          //           borderWidth: 2
          //       }
          //   },
          // }
        ]
      }
      return {
        chartData: {
        //  columns: ['list', 'percent'],
          columns: ['list'],
          rows: [
              {list: 'Node',percent: 0.72},
            // {list: 'Php',percent: 0.71}
          ]
        }
      }
    }
  }
</script>



