<template>
  <div class="row">
    <div class="col-md-6 offset-md-3 col-sm-10 offset-sm-1 mt-5">
      <form id="register-form" role="form" @submit.prevent="onSubmit">
        <h3 class="text-center">註冊</h3>
            <div class="form-group mt-5">
          <input
            type="txt"
           
            id="name"
            class="form-control"
            placeholder="姓名"
            v-model="users.name"
            required
          />
        </div>
        <div class="form-group">
          <input
            type="email"
           
            id="email"
            class="form-control"
            placeholder="Email Address"
            value
            v-model="users.email"
            required
          />
        </div>
          <div class="form-group">
          <input
            type="txt"
            
            id="phone"
            class="form-control"
            placeholder="Phone"
            value
            v-model="users.phone"
            required
          />
        </div>
        <div class="form-group">
          <input
            type="password"
           
            id="password"
            class="form-control"
            placeholder="Password"
            v-model="password"
            required
          />
        </div>
        <div class="form-group">
          <button type="submit" class="btn btn btn-primary" style="width: 100%" :disabled="isLoading">
            <i v-if="isLoading" class="fa fa-spinner fa-spin" />
            註冊
          </button>
        </div>
        <div class="form-group">
          <div class="row">
            <div class="col-lg-12">
              <div class="text-center">
                <router-link to="/login">
                  <a>Login</a>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from '@/config/firebaseConfig';
import { mapActions } from 'vuex';
import firebase from 'firebase/compat/app'
const usersRef = firebase.database().ref("/users")
export default {
  data() {
    return {
      email: '',
      password: '',
      phone:'',
      isLoading: false,
        users: {
          username: '',
          email: '',
          phone: ''
        }
    }
  },
  
  methods: {
    ...mapActions(['clearMessage', 'addMessage', 'registerByEmail']),
    onSubmit(event) {
      this.isLoading = true
      let data = {
        email: this.users.email,
        password: this.password
      }
      console.log(data)
      this.registerByEmail(data).then(() => {
        this.clearMessage();
        this.$router.push({ name: 'User' });
      })
        .catch((error) => {
          // console.log('register error', error);
          let message_obj = {
            message: error.message,
            messageClass: "danger",
            autoClose: true
          }
          this.addMessage(message_obj);
        }).then(() => {
          this.isLoading = false
        })
       
            usersRef.push({
            num:`${new Date().toLocaleDateString()} ${new Date().toLocaleTimeString()}`,
            name:this.users.name,
            phone:this.users.phone,
            email:this.users.email,
             timeStamp: new Date(),
             })
             .then(() => {console.log('');});
              event.preventDefault()
    }
  }
}
</script>



