import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import VueAxiosLesson from '@/components/VueAxiosLesson'
import Charts from '@/components/Charts'
import Login from '@/components/auth/Login'
import Register from '@/components/auth/Register'
import User from '@/components/auth/User'
import Products from '@/components/Products'
import Practise from '@/components/Practise'
import newCalendar from '@/components/newCalendar'
import Css from '@/components/layouts/effect/Css'
import Dasboard from '@/components/auth/Dasboard'
Vue.use(Router)

export default new Router({
    mode: "history",
    routes: [
        { path: '*', redirect: '/' },
        {
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/vueaxios',
            name: 'VueAxiosLesson',
            component: VueAxiosLesson
        },
        {
            path: '/charts',
            name: 'Charts',
            component: Charts
        },
        {
            path: '/css',
            name: 'Css',
            component: Css
        },
        {
            path: '/practise',
            name: 'Practise',
            component: Practise
        },
        {
            path: '/products',
            name: 'products',
            component: Products,

        },
        {
            path: '/newcalendar',
            name: 'newCalendar',
            component: newCalendar,

        },
        {
            path: '/login',
            component: Login,
            name: 'login',
            onlyGuest: true
        },

        {
            path: '/register',
            component: Register,
            name: 'register',
            onlyGuest: true
        },

        {
            path: '/admin',
            component: Dasboard,
            name: 'Dasboard',
            //需要驗證
            meta: { requiresAuth: true },
            children: [{
                path: '/user',
                component: User,
                name: 'User',
            }]
        }
    ]
})


