<template>
    <div>
    <nav class="navbar navbar-expand PortFolio">
      <div class="container">
          <router-link to="/" class="navbar-brand">Lara PortFolio</router-link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExample02" aria-controls="navbarsExample02" aria-expanded="false" aria-label="Toggle navigation"  @click="toggleNavbar">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarsExample02">
          <ul class="navbar-nav mr-auto">    
          </ul>   
          <ul class="navbar-nav my-2 my-md-0">
            <li class="nav-item active">
              <form class="form-inline my-2 my-md-0">
                <input class="form-control" type="text" placeholder="Search">
              </form>
            </li>
            <li class="nav-item">
              <router-link to="/products" class="nav-link">
              products
              </router-link>
            </li>
            <li class="nav-item">
              <router-link to="/" class="nav-link">
              作品集
              </router-link>
            </li>
             <li class="nav-item">
              <router-link to="/charts" class="nav-link">
               圖表 </router-link> 
            </li>
            <li class="nav-item"  @click="logout" v-if="loggedIn" >
              <router-link class="nav-link">
                  登出 {{ userEmail }}
              </router-link>
            </li>
            <li  class="nav-item" v-else >
              <router-link to="/login" class="nav-link">
                  登入
              </router-link>
            </li>
            <li class="nav-item" v-if="!isLoggedIn" >
              <router-link to="/register" class="nav-link">
                  註冊
              </router-link>
            </li>       
            <li class="nav-item">
                <router-link class="nav-link" to="/css"  >
                Css
                </router-link>  
            </li >    
          </ul>
        </div>
      </div>
    </nav>


    </div>
</template>

<script>

import Cookies from 'js-cookie'
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth'
import {mapActions, mapGetters} from 'vuex';
export default {
  data() {
    return {
      isNavOpen: false,
        see:true,
      loggedIn: false
    }
  },
  mounted() {
    this.setupFirebase()
  },
  computed: {
    ...mapGetters(['isLoggedIn',  'currentUser']),
    numItems() {
      return this.cartItemList.reduce((total, item) => {
        total += item.quantity;
        return total
      }, 0);
    },
    userEmail() {
      return this.isLoggedIn ? this.currentUser.email : ''
    }
  },
  methods: {
    ...mapActions(['logout']),
    toggleNavbar() {
      this.isNavOpen = !this.isNavOpen
    },

     setupFirebase() {
      firebase.auth().onAuthStateChanged(user => {
        if (user) {
          // 用戶已登錄
          console.log('用戶已登錄')
          firebase
            .auth()
            .currentUser.getIdToken(true)
            .then(token => Cookies.set('access_token', token))

          this.loggedIn = true
        } else {
          Cookies.remove('access_token')

          // if (Cookies.set('access_token', 'blah')) {
          // }

          // 沒有用戶登錄
          this.loggedIn = false
          console.log('signed out登出', this.loggedIn)
        }
      })
    },

    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace({ name: 'login' })
        })
    }
  }
}

</script>



