<template>
  <div>
 <ve-radar 
  :data="chartData" 
  :settings="chartSettings"
  :extend="chartExtend"
  :legend-visible="false"
  
  style="height:50%;width:100%;margin-top:20px"
  ></ve-radar>
  </div>
</template>

<script>
  export default {
    data () {
    this.title = {
        text: "階層式樣式表",
        x: "center",
        textStyle: {
        color: "#000",//标题颜色
        fontSize: 16  //字体大小
        }
    };
    this.chartSettings = {
        tooltipVisible:"true",
        labelMap: {
            '項目': 'date',
            '動態': 'Animation',
            '下单用户': '下单用户',
            '下单率': '下单率',
            '第一产业': '第一产业',
            '@keyframes': '@keyframes',
        },
        areaStyle: {
            type: 'default',
            opacity: 0.8, 
            color: ['rgba(200,100,154,.5)']
        },
    };
      this.legend =  { enabled: false };

      this.chartExtend = {
        series(v){
          v.forEach(i => {
            console.log(i)
            // i.radar.center = ["50%", "85%"]
          })
          
          return v
        }
      };
      return {
        chartData: {
          columns: ['細目', 'Animation', '下单用户', '下单率', '第一产业', '@keyframes'],
          rows: [
            { '項目': 'Scss', 'Animation': 1393, '下单用户': 1093, '下单率': 0.32, '第一产业': 1000, '@keyframes': 1444 },
            { '項目': 'Css', 'Animation': 3530, '下单用户': 3230, '下单率': 0.26, '第一产业': 1000, '@keyframes': 1444 },
          
          ]
        }
      }
    }
  }
</script>


