<template>
    <div>
<nav class="navbar navbar-expand PortFolio">
  <router-link to="/" class="navbar-brand" href="#">Lara PortFolio</router-link>

  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExample05" aria-controls="navbarsExample05" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="conatiner collapse navbar-collapse" id="navbarsExample05">
<div class="form-inline  mr-auto "></div>
    <form class="form-inline  mr-auto hidden">
      <input class="form-control " type="text" placeholder="Search">
    </form>
   
     <ul class="navbar my-2 my-md-0 newNav hidden">
         <li v-if="loggedIn" > 
             <a @click="logout" href="/login" class="logout-link">登出</a></li>
    
      <li  v-else class="nav-item">
        <a class="nav-link" href="/login" >登入</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" href="/register">註冊</a>
      </li>
      <li class="nav-item hidden">
        <a class="nav-link" href="/newcalendar">calendar</a>
      </li>
      <li class="nav-item dropdown hidden">
        <a class="nav-link dropdown-toggle" href="#" id="dropdown05" data-toggle="dropdown" aria-expanded="false">Dropdown</a>
        <div class="dropdown-menu" aria-labelledby="dropdown05">
          <a class="dropdown-item" href="#">Action</a>
          <a class="dropdown-item" href="#">Another action</a>
          <a class="dropdown-item" href="#">Something else here</a>
        </div>
      </li>
    </ul>
  </div>
</nav>
    </div>
</template>

<script>
import Cookies from 'js-cookie'
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth'
export default {
 data() {
    return {
        see:true,
      loggedIn: false
    }
  },
   mounted() {
    this.setupFirebase()
  },
  asyncData() {},
  methods: {
    setupFirebase() {
      firebase.auth().onAuthStateChanged(user => {
        if (user) {
          // 用戶已登錄
          console.log('用戶已登錄')
          firebase
            .auth()
            .currentUser.getIdToken(true)
            .then(token => Cookies.set('access_token', token))

          this.loggedIn = true
        } else {
          Cookies.remove('access_token')

          // if (Cookies.set('access_token', 'blah')) {
          // }

          // 沒有用戶登錄
          this.loggedIn = false
          console.log('signed out登出', this.loggedIn)
        }
      })
    },

    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace({ name: 'login' })
        })
    }
  }
}
</script>



