<template>
  <div>
 <ve-histogram  :data="chartData"
  :extend = "chartExtend"   ></ve-histogram>
 
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
   data () {
        this.chartExtend = {
            series (v) {
                v.forEach((i, index )=> {
                    i.itemStyle = {
                        normal: {
                            color: function(params) {
                            // build a color map as your need.
                            var colorList = [
                              '#40b883','#009688','#d5ba32','#0667ac','#cd6a9b'
                            ];
                            return colorList[params.dataIndex]
                        },
                        },
                      
                    }
                   // console.log(i,index)
                })
                return v
            },
      }
      return {
        label:{
          fontWeight:'bolder'
        },
        chartData: {
          columns: ['項目', '前端技術',],
          rows: [
            { '項目': 'Vue', '前端技術': 95, },
            { '項目': 'Nuxt', '前端技術': 70,  },
            { '項目': 'JavaScript', '前端技術': 89,  },
            { '項目': 'Jquery', '前端技術': 89,  },
            { '項目': 'Scss & Css', '前端技術': 94,  },
          ]
        }
      }
    }
  
}
</script>


