<template>
  <div>
  <ve-pie  :data="chartData"
  :extend = "chartExtend"></ve-pie>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
   data () {
        this.chartExtend = {
            series (v) {
                v.forEach((i, index )=> {
                    i.itemStyle = {
                        normal: {
                            color: function(params) {
                            // build a color map as your need.
                            var colorList = [
                              '#C1232B','#B5C334','#FCCE10','#E87C25','#27727B',
                               '#FE8463','#9BCA63','#FAD860','#F3A43B','#60C0DD',
                               '#D7504B','#C6E579','#F4E001','#F0805A','#26C0C0'
                            ];
                            return colorList[params.dataIndex]
                        },
                        }
                    }
                    console.log(i,index)
                })
                return v
            },
      }
      return {
        chartData: {
          columns: ['日期', '訪問用戶', ],
          rows: [
            { '日期': '1/1', '訪問用戶': 1393, },
            { '日期': '1/2', '訪問用戶': 3530,  },
            { '日期': '1/3', '訪問用戶': 2923,  },
            { '日期': '1/4', '訪問用戶': 1723,  },
            { '日期': '1/5', '訪問用戶': 3792,  },
            { '日期': '1/6', '訪問用戶': 4593,  }
          ]
        }
      }
    }
  /*data () {
    return {
      colors:['#00CC99', '#CC0066', '#FF6600', '#3366CC','#FFCC00','#663399'],
       chartData: {
        columns: ['項目', '访问用户'],
        rows: [
            { '項目': 'Ｖue', '访问用户': 1393 },
            { '項目': 'Scss & Css', '访问用户': 3792},
            { '項目': 'Ｈtml', '访问用户': 3792 },
            { '項目': 'jquery  & javascript', '访问用户': 2923 },
            { '項目': 'PHP & Node', '访问用户': 1723 },
            { '項目': 'MySQl', '访问用户': 1723 },
        ]
      },
    }
  }*/
}
</script>


