<script>
export default{
name:'Calendar',
 components: {   
      "AddList": ()=>import("@/components/modal/AddList"),
  },
    data(){
    return {
      StartDate:'',
      msgStartDateError:[],
      EndDate:'',
      msgEndDateError:[],
      StartTime:'',
      msgStartTimeError:[],
      EndTime:'',
      msgEndTimeError:[],
      msgWeekDaysError:[],
      subject:'',
      msgSubjectError:[],
      Describe:'',
      msgDescribeError:[],
      isDisabled:false,
      
      weekDays: [
           {id:0,value:0,Week:'Sun'},
           {id:1,value:1,Week:'Mon'},
           {id:2,value:2,Week:'Tue'},         
           {id:3,value:3,Week:'Web'},
           {id:4,value:4,Week:'Thr'},
           {id:5,value:5,Week:'Fri'},
           {id:6,value:6,Week:'Sat'},
      ],
      selected: [],
      today:{
           year:0,
           month:0,
           date:0,
           day:0
       },
       calendar:{
           year:0,
           month:0,
           date:0,
           day:0
       },
            CurrentMonth:null,
            shareDate: new Date() ,
           todes:[
             {id:1,work:'學習',date:'2022-05-01',startTime:"00:00",endTime:"00:30"},
             {id:2,work:'旅行',date:'2022-05-01',startTime:"08:00",endTime:"08:30"},
             {id:3,work:'學開車',date:'2022-05-01',startTime:"10:00",endTime:"10:30"},
             {id:4,work:'學開車',date:'2022-05-05',startTime:"10:00",endTime:"10:30"}
           ]
         }
    },
    mounted(){
      this.setToday();    
    },
    methods:{
      //設定今天
      setToday(){
         const date = new Date();
         const vm=this;
         vm.today.year = vm.calendar.year = date.getFullYear();//年
         vm.today.month = vm.calendar.month = date.getMonth();//月 0~11
         vm.today.date = vm.calendar.date = date.getDate();//日期
         vm.today.day = vm.calendar.day = date.getDay(); //星期
         console.log("today",vm.today)
         console.log("calendar",vm.calendar)
         this.ChangeMonth();
      },
       //上個月
       prevMonth() {
          const vm=this;
          vm.calendar.month--; 
          if (vm.calendar.month == 0) {
            vm.calendar.month = 11;
            vm.calendar.year--;
          }
       },
      //下個月
      nextMonth() {
        const vm=this;
        vm.calendar.month++; 
         if ( vm.calendar.month >11) {
            vm.calendar.month = 0;
            vm.calendar.year++;
          }
      },

      //調整年份
      adjustYear(fix){
          const vm=this;
          vm.calendar.year += fix;
       },
  //調整月份
     adjustMonth(fix){
       const vm=this;
       // this.calendar.month += fix 範圍
       let month = vm.calendar.month + fix
       if(month > 11){
          vm.adjustYear(1)
          vm.calendar.month = 0
       }else if(month < 0){
          vm.adjustYear(-1)
          vm.calendar.month = 11
       }else{
           vm.calendar.month = month
       }
      },
       //小於10的數字補0
       pad(str) {
           return str < 10 ? `0${str}` : str;
       },
       //格式化時間
       stringify(year, month, date) {
         let str = [year, this.pad(month + 1), this.pad(date)].join('-');
         return str;
       },
      //showAddList
      showAddList(){
        const vm = this;
        $('#addListeModal').modal('show');
        //清掉所有驗證提示
        vm.isDisabled=false;
        vm.msgStartDateError=[];
        vm.msgEndDateError=[];
        vm.msgWeekDaysError=[];
        vm.msgStartTimeError=[];
        vm.msgEndTimeError = [];
       vm.msgSubjectError = [];
      vm.msgDescribeError = []; 
      },
      //驗證
      checkForm(e){
      const vm=this;
       e.preventDefault(); 
       vm.isDisabled=false;
    
      if( vm.StartDate === '' || vm.EndDate==='' || vm.StartTime === '' || vm.EndTime==='' ||vm.weekDays==='' || vm.subject==='' || vm.Describe==='') {
         vm.msgStartDateError.push("Start Date is required.");
         vm.msgEndDateError.push("End Date is required.");
        vm.msgWeekDaysError.push("WeekDays is required.")
         vm.msgStartTimeError.push("Start Time is required.");
         vm.msgEndTimeError.push("End Time is required.")   
        vm.msgSubjectError.push("Subject is required.")
       vm.msgDescribeError.push("Describe is required.")
         vm.isDisabled=true;
      
      } else{
         vm.isDisabled=false;
         alert('已傳送');
        
      }
    },
},
  
computed:{
  //月份的第1天
  calendarFirstDay(){
     const vm =this;
     const mDate = new Date(vm.calendar.year,vm.calendar.month,1)
     const date = new Date(vm.calendar.year,vm.calendar.month,1 - mDate.getDay())
        return {
           year:date.getFullYear(),
           month:date.getMonth(),
           date:date.getDate(),
            day:date.getDay(),
        }
  },
  //萬年曆
  calendarMonth(){
     const data = []
     let date
     for(let i=0;i<42;i++){
      date = new Date(this.calendarFirstDay.year,this.calendarFirstDay.month,this.calendarFirstDay.date + i);
            data.push({
                  year:date.getFullYear(),
                  month:date.getMonth(),
                  date:date.getDate(),
                  day:date.getDay(),
                  value: this.stringify(date.getFullYear(), date.getMonth(), date.getDate()),
            })
        }
        console.log(data)
        return data
      },
  //Select All
   selectAll: {
      get() {
        const vm=this;
        if (vm.weekDays && vm.weekDays.length > 0) { 
          let allChecked = true;

          for (const week of vm.weekDays) {
            if (!vm.selected.includes(week.id)) {
              allChecked = false; 
            }           
            if(!allChecked) break;
          }
          return allChecked;
        }
        return false;
      },
      set(value) {
        const vm =this;
        const checked = [];

        if (value) {
          vm.weekDays.forEach((week) => {
            checked.push(week.id);
          });
        }
        vm.selected = checked;
      }
    },
  
  } 

}

</script>

<style lang="scss" scoped>
