//input,textarea
input[type=date].form-control, input[type=datetime-local].form-control, input[type=month].form-control, input[type=time].form-control,
input[type=text].form-control,
textarea.form-control{
  border:1px solid #3366cc;
}
//checkbox
.checkBox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  }
}

.square-checkmark,.round-checkmark  {
  position: absolute;
  top: 0;
  left: 0;
  height: 2rem;
  width: 2rem;
  background-color: #ffff;
  border: 2px solid #3366cc;
  border-radius: 4px;
}
.square-checkmark{
  border-radius: 4px;
}
.round-checkmark {
  border-radius: 50%; 
}

.checkBox:hover input ~ .round-checkmark,
.checkBox:hover input ~ .square-checkmark{
   border: 3px solid #2196f3;
}

.checkBox input:checked ~ .round-checkmark,
.checkBox input:checked ~ .square-checkmark{
   background: #2196f3;
}

.round-checkmark:after ,
.square-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkBox input:checked ~ .round-checkmark:after ,
.checkBox input:checked ~ .square-checkmark:after {
  display: block;
}

.checkBox .round-checkmark:after,
.checkBox .square-checkmark:after {
  left: 9px;
  top: -10px;
  width: 1rem;
  height: 2rem;
  border: solid #ffff;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
//
.round-checkmark.disabled,
.square-checkmark.disabled,

.checkBox:hover input ~ .round-checkmark.disabled,
.checkBox:hover input ~ .square-checkmark.disabled,
.checkBox input:checked ~ .round-checkmark.disabled,
.checkBox input:checked ~ .square-checkmark.disabled{
   cursor: not-allowed; //not-allowed,wait ,help,crosshair,zoom-in,grab,cell,text,alias,copy,move,zoom-in
   background-color: #ddd;
  border:2px solid #ddd;
}
.checkBox .round-checkmark.disabled:after,
.checkBox .square-checkmark.disabled:after,
.checkBox input:checked ~ .round-checkmark.disabled:after, 
.checkBox input:checked ~ .square-checkmark.disabled:after {
    display:none;
}
//weekDays
.weekDays-chkBoxSquared {
      display: inline-block;
      width: 62px;
      color: #0099FF;
     & input[type=checkbox] {
          display: none;
      }
     & label {
          cursor: pointer;
          color:#3366cc; 
          display: inline-block;
          position: relative;
          width: 60px;
          line-height: 60px;
          text-align: center;
          margin: auto 5px;
          background:#ffff;
          border-radius: 8px;
      }
      & label:before {
          position: absolute;
          content: "";
          width: 60px;
          height: 60px;
          left: 0px;
          top: 0px;
          border: 2.5px solid #3366cc; 
          border-radius: 8px;
          color: #3366cc;
      }
     & label:after {
          position: absolute;
          content: "";
          width: 60px;
          height: 60px;
          left: 0px;
          top: 0px;
          border-radius: 8px;
          
      }
      //
     & label:before {
          position: absolute;
          content: "";
          width: 60px;
          height: 60px;
          left: 0px;
          top: 0px;
      }
     & input[type=checkbox]:disabled+label:before,
     & input[type=checkbox]:disabled+label:after {
          display: none;
      }
     & label:hover::after {
          border: 3px solid #0099ff;
          color:#0099ff;
      }
    & input[type=checkbox]:checked+label {
          color: #fff;
          font-weight: 600;
          z-index: 1;
      }
     & input[type=checkbox]:checked+label:after {
          border: 3px solid #0099ff;
          //  background-image: url('@/assets/img/logo.png');
          position: absolute;
          content: '';
          width: 60px;
          height: 60px;
          background-color: #3366cc;
          z-index: -1;
      }
  }


</style>



